
import { log } from '../Store/Log';

export class Canvas {
    /* The actual elements and canvases */
    protected renderingCanvas: any;
    protected renderingContext: CanvasRenderingContext2D;
    protected canvas: HTMLCanvasElement;

    constructor(
    ) {
        this.renderingCanvas = document.createElement( 'canvas' );
        this.renderingContext = this.renderingCanvas.getContext( '2d' );

        this.renderingCanvas.height = screen.height;
        this.renderingCanvas.width  = screen.width;

        // document.body.appendChild( this.renderingCanvas );

        this.renderingCanvas.imageSmoothingEnabled = false;
        this.renderingCanvas.mozImageSmoothingEnabled = false;
        this.renderingCanvas.webkitImageSmoothingEnabled = false;
        this.renderingCanvas.msImageSmoothingEnabled = false;
    }
}
