import { Vue } from 'vue-property-decorator';
import { clone } from '@components/Property/Property';

enum Level {
    Error = 'error',
    Warning = 'warn',
    Info = 'info',
    Debug = 'debug'
}

interface Message {
    level: Level;
    message: string;
    time: Date;
    params?: Object;
}

const LogStore: Object = {
    namespaced: true,
    state: {
        maxSize: 100,
        messages: []
    },
    getters: {
        errors: state => {
            return state.messages.filter( ( record ) => record.level === Level.Error );
        }
    },
    mutation: {},
    actions: {
        async log( context, payload ) {
            const message: Message = {
                level: payload.level,
                message: payload.message,
                params: clone( payload.params ),
                time: payload.time
            };

            Vue.nextTick( () => {
                const details = context.state.messages;
                details.unshift( message );
                details.splice( Math.min( details.length, context.state.maxSize ) );
                Vue.set( context.state, 'messages', details );
            } );

            console[ payload.level ]( payload.message, payload.params, payload.context  );

        },
        async clear( context, payload ) {
            Vue.set( context.state, 'messages', [] );
        }
    }
};

class Log {
    constructor() {
        /* Set up an initial cache of messages and a store */
        this.$store = {
            dispatch: this.dispatch
        };
    }

    private cache = [];
    private dispatch( op, message ) {
        this.cache.push( { op: op, message: message } );
    }

    private $store: any = null;

    public setStore( store ) {
        this.$store = store;

        /* having attached the store, put any cached messages
           into the store */
        this.cache.forEach( ( ob ) => {
            this.$store.dispatch( ob.op, ob.message );
        } );
    }

    private log( message: any = {} ) {
        /* take a copy of anything passed in, do *not* store the actual
           values or pointers to them */
        message.params = message.params.map( ( param ) => {
            if ( param === this.$store ) {
                return clone( param.state );
            }
            return clone( param );
        } );

        message.time = new Date();

        this.$store.dispatch( 'log/log', message  );
    }

    public error( message: string, ...params: any[] ) {
        this.log( { message: message, level: Level.Error, params: params } );
    }
    public warn( message: string, ...params: any[] ) {
        this.log( { message: message, level: Level.Warning, params: params } );
    }
    public info( message: string, ...params: any[] ) {
        this.log( { message: message, level: Level.Info, params: params } );
    }
    public debug( message: string, ...params: any[] ) {
        this.log( { message: message, level: Level.Debug, params: params } );
    }


    public clear() {
        this.$store.dispatch( 'log/clear', {} );
    }

    public messages(): Message[] {
        return this.$store.state.log.messages;
    }

}

export const log: Log = new Log();

function init( store, product ) {
    /* Initialise the log store ... */
    const logstore = Object.assign( {}, LogStore );
    store.registerModule( 'log', logstore );
    log.setStore( store );
    return log;
}


function properties( app ) {
    /* Initialise the store with the user accessors */
    Object.defineProperties( app, {
        $log: {
            get() {
                return log;
            }

        }
    } );
}

export { init, Level, Message, properties };
