import { RateLimitedCanvas } from './rateLimitedCanvas';

import { log } from '../Store/Log';

export class AmplitudeCanvas extends RateLimitedCanvas {
    protected widthFactor: number = 0;
    protected heightFactor: number = 1;
    protected widthSamplesPerPixel: number;

    constructor() {
        super();
    }

    protected setScale( numberOfAmplitudes ): boolean {
        /* determine what the scale should be */
        try {
            let rescaled = false;

            /* resize the final, actual canvas */
            const newHeight = Math.max( this.canvas.clientHeight, 100 );
            const newWidth  = Math.max( this.canvas.clientWidth, 100 );

            if ( this.canvas.height !== newHeight ) {
                this.canvas.height = newHeight;
            }
            if ( this.canvas.width !== newWidth ) {
                this.canvas.width = newWidth;
            }

            const width  = this.canvas.width;
            const height = this.canvas.height;

            /* Determine how many amplitudes we can (or should) show per pixel */

            this.widthSamplesPerPixel = Math.min( numberOfAmplitudes / width, 10000 );

            let widthFactor = numberOfAmplitudes / Math.ceil( this.widthSamplesPerPixel );
            const truewidth = Math.floor( widthFactor );
            widthFactor = ( width / truewidth );

            if ( this.widthFactor !== widthFactor ) {

                /* Set the Reducer accordingly */
                this.widthFactor = widthFactor;

                rescaled = true;
            }

            if ( rescaled ) {
                return this.rescale();
            }
        } catch ( e ) {
            log.warn( 'Amplitude Canvas Rescale failed: ', e );
        }
        return false;
    }

    /* Virtual function - should be overridden and will be called whenever
     * the scale of the canvas changes */
    protected rescale(): boolean {
        return true;
    }
}
