import omit from 'lodash-es/omit';
import isEqual from 'lodash-es/isEqual';
import { cloneDeepWith, cloneDeep as clonedeep, isElement } from 'lodash-es';
import { Vue } from 'vue-property-decorator';

export function clone( property ): any {
    try {
        if ( property === null ) {
            return null;
        } else if ( Array.isArray( property ) ) {
            return clonedeep( property );
        } else if ( typeof( property ) === 'object' ) {
            return clonedeep( omit( property, [ 'dialog' ] ) );
        } else {
            return property;
        }
    } catch ( e ) {
        console.error( 'Cloning of ', property, 'failed', e );
        return null;
    }
}

/* CloneDeep and CloneDeepWith (and Clone) will also clone reactivity
   meaning that any reactive side effects of manipulating properites
   are preserved too ... */

export function nonReactiveClone( property ) {
    /* we need to flatten out and remove distasteful items from the object
        we wish to clone, such as HTML elements etc */
    try {
        const sanitised = cloneDeepWith( property, ( element ) => {
            if (
                ( element instanceof Vue ) || ( element && element._isVue ) ||
                ( element instanceof HTMLElement ) ||
                ( element instanceof HTMLInputElement ) ||
                ( element instanceof HTMLFormElement ) ||
                ( element instanceof HTMLCollection ) ||
                ( element instanceof Symbol ) ||
                ( element instanceof HTMLDocument ) ||
                ( isElement( element ) ) ||
                ( typeof( element ) === 'function' )
            ) {
                return false;
            }
            return element;
        } );
        return JSON.parse( JSON.stringify( sanitised ) );
    } catch ( e ) {
        console.log( 'Unable to clone object', property, e );
    }
    return null;
}

export function compare( a: any, b: any ): boolean {
    const reduce = ( obj ) => {
        let reduced: any = String( obj );
        /* Take a look at the property obj and choose what to do with it */
        if ( obj && typeof( obj ) === 'object' ) {
            /* It is an object of some form ... */
            if ( Array.isArray( obj ) ) {
                reduced = obj.map( ( e ) => reduce( e ) );
            } else if ( 'value' in obj ) {
                /* If we have a value, assume that the value
                   is the thing we want to use .. */
                if ( 'disabled' in obj && obj[ 'disabled' ] ) {
                    /* disabled properties are always equal .. */
                    reduced = true;
                } else {
                    reduced = reduce( obj[ 'value' ]  );
                }
            } else {
                /* IF it's just a normal object, recurse through
                   it and reduce that too ... */
                reduced = Object.keys( obj ).reduce(
                    ( prev, key ) => ( prev[ key ] = reduce( obj[ key ] ), prev ), {}
                );
            }
        }
        /* try parsing the reduced form as a Number .. */
        try {
            const aNumber = Number( reduced );
            if ( !isNaN( aNumber ) ) {
                reduced = aNumber;
            }
        } catch ( e ) {
            /* it wasn't a number ... nevermind */
        }

        return reduced;
    };

    if ( typeof( a ) === 'object' ) {
        return !isEqual( reduce( a ), reduce( b ) );
    } else {
        /* Numbers ? */
        try {
            const aNumber = Number( a );
            const bNumber = Number( b );

            if ( !isNaN( aNumber ) && !isNaN( bNumber ) ) {
                return aNumber !== bNumber;
            }
        } catch ( e ) {

        }
    }
    return a !== b;
}

export function findProperty( where: object, item: string ) {
    if ( typeof( where ) === 'object' ) {
        if ( where[ 'field' ] && where[ 'field' ] === item ) {
            return where;
        } else if ( where[ 'properties' ] ) {
            if ( Array.isArray( where[ 'properties' ] ) ) {
                return ( where[ 'properties' ] as any ).find( ( sub ) => findProperty( sub, item ) );
            } else {
                return findProperty( where[ 'properties' ], item );
            }
        }
    }
    return null;
}

export function addDetail( dialog: any, property: string, details: any ): void {
    /* Find the detailed property within the dialog */
    const prop = findProperty( dialog, property );

    if ( prop ) {
        prop.options = Object.assign( {}, prop.options, clone( details ) );
    }
}
