export class Colour {
    private colours: Object = {};
    private min: number;
    private max: number;

    constructor() {
        this.setPalette( 'Default' );
    }

    private palettes: any = {
        'Greys': [ [ 0, 255, 255, 255 ], [ 1, 25, 25, 25 ] ],
        'Heat': [ [ 0, 255, 255, 204 ], [ 0.125, 255, 237, 160 ], [ 0.25, 254, 217, 118 ], [ 0.375, 254, 178, 76 ], [ 0.5, 253, 141, 60 ], [ 0.625, 252, 78, 42 ], [ 0.75, 227, 26, 28 ], [ 0.875, 189, 0, 38 ], [ 1, 128, 0, 38 ] ],
        'Sea':  [ [ 0, 255, 255, 217 ], [ 0.125, 237, 249, 177 ], [ 0.25, 199, 233, 180 ], [ 0.375, 127, 204, 187 ], [ 0.5, 64, 182, 196 ], [ 0.625, 29, 145, 192 ], [ 0.75, 34, 94, 168 ], [ 0.875, 37, 51, 148 ], [ 1, 7, 29, 88 ] ],
        'Default': [ [ 0, 0, 0, 255 ], [ 0.25, 255, 255, 0 ], [ 1, 255, 0, 0 ] ]
    };

    public getPalettes(): string[] {
        return Object.keys( this.palettes );
    }

    public setPalette( palette: string ) {
        let actualPalette = this.palettes[ 'Default' ];
        if ( this.palettes[ palette ] ) {
            actualPalette = this.palettes[ palette ];
        }
        this.controlPoints.length = 0;
        this.colours = {};
        for ( let i = 0; i < actualPalette.length; ++i ) {
            this.addControlPoint.apply( this, actualPalette[ i ] );
        }
    }

    public addControlPoint( amplitude, red, green, blue  ) {
        const cp = new ControlPoint( amplitude, red, green, blue );
        this.controlPoints.push( cp );
    }

    private controlPoints: ControlPoint[] = [];

    public setRange( min, max ) {
        this.min = min;
        this.max = max;
        this.colours = {};
    }

    public getRange() {
        return { min: this.min, max: this.max };
    }

    private round10( num, pow ): number {
        if ( pow >= 0 ) {
            const scale = Math.pow( 10, pow );
            return Math.round( num / scale ) * scale;
        } else {
            const scale = Math.pow( 10, -pow );
            return Math.round( num * scale ) / scale;
        }
    }

    public get( realAmplitude ): ControlPoint {
        let amplitude = realAmplitude;
        amplitude = Math.max( amplitude, this.min );
        amplitude = Math.min( amplitude, this.max );

        amplitude = ( ( amplitude - this.min ) / ( this.max - this.min ) );

        amplitude = this.round10( amplitude, -6 );

        if ( !this.colours[ amplitude ] ) {

            let cp0 = this.controlPoints[ 0 ];
            if ( amplitude < cp0.amplitude ) {
                return cp0;
            }

            for ( let i = 1; i < this.controlPoints.length; ++i ) {
                const cp1 = this.controlPoints[ i ];
                if ( amplitude === cp1.amplitude ) {
                    return cp1;
                }
                if ( amplitude > cp1.amplitude ) {
                    cp0 = cp1;
                    continue;
                }
                const percent = Math.min( ( ( amplitude - cp0.amplitude ) / ( cp1.amplitude - cp0.amplitude ) ), 100 );

                const red = this.interp( percent, cp0.red, cp1.red );
                const green = this.interp( percent, cp0.green, cp1.green );
                const blue = this.interp( percent, cp0.blue, cp1.blue );

                this.colours[ amplitude ] = new ControlPoint( amplitude, red, green, blue );
                break;
            }
        }

        return this.colours[ amplitude ];
    }

    private interp( percent, i0, i1 ): number {
        return Math.round( i0 + ( percent * ( i1 - i0 ) ) );
    }
}

export class ControlPoint {
    constructor( public amplitude, public red, public green, public blue ) {}

    public colour(): string {
        const colour = Math.min( this.red, 255 ) << 16 | Math.min( this.green, 255 ) << 8 | Math.min( this.blue, 255 );
        return ( function( h ) {
            return '#' + new Array( 7 - h.length ).join( '0' ) + h;
        } )( colour.toString( 16 ).toUpperCase() );
    }
}
