/* Reducer
 * Data Reducer node for a Fotech FibreStream
 *
 * Provides a simple mechanism for containing (retaining) fibreshots
 * which are reduced in size by some factor in terms of time and
 * width
 *
 * takes in a scale factor and a callback function which is called
 * whenever a "new" temporally downsampled firbreshot has been created
 *
 * Implemented using a worker thread to actually perform the calculation
 * meaning that for the most part this class is a message passing system
 * with potential queueing.
 */

import { Ant } from './ants';

export default class Reducer extends Ant {
    private widthFactor: number;
    private timeFactor: number;
    private mode: string;

    private _rawFibreShots: any = [];
    private _modes: any;

    constructor( { callbacks = {}, width = 10, time = 10, mode = 'magnitude' } ) {
        super();
        this.setModes();
        this.setMode( mode );
        this.setWidth( width );
        this.setTime( time );
    }

    public setWidth( width ) {
        this.widthFactor = width;
    }

    public setTime( time ) {
        this.timeFactor = time;
    }

    public setMode( mode ) {
        if ( this._modes[ mode ] ) {
            this.mode = mode;
        }
    }

    public clear() {
        this._rawFibreShots.length = 0;
    }

    public add( fibreShot ) {
        const timeEpsilon = 10000;  // time diff of more than 10 seconds as suggested by george
        let reset = false;

        /* Compensate for MUX units (or other things) changing the starting position of the
        * fibreshot (and most likely its length too) as we cannot temporally decimate two
        * fibreshots which aren't even remotely alike) */

        /* Compensate for great time differences (temporal discontinuity) */
        if (
            this._rawFibreShots.length > 0
            &&
            (
                /* Channel IDs (true MUXing) */
                this._rawFibreShots[ 0 ].channel !== fibreShot.channel
                ||
                this._rawFibreShots[ 0 ].startingPosition !== fibreShot.startingPosition
                ||
                this._rawFibreShots[ 0 ].amplitudes.length !== fibreShot.amplitudes.length
                ||
                /* Time discontinuity - Time running backwards */
                fibreShot.time < this._rawFibreShots[ 0 ].time
                ||
                /* Time discontinuity - Huge time gap */
                Math.abs( fibreShot.time - this._rawFibreShots[ 0 ].time ) > timeEpsilon
            )
        ) {
            reset = true;
        }

        if ( !reset ) {
            /* We can simply add this entry as it is equivalent to those we already have */
            this._rawFibreShots.push( fibreShot );
        }

        if ( this._rawFibreShots.length >= this.timeFactor || reset ) {
            /* we need to clone a fibreshot, or at least take the most recent */
            const amplitudes = [];

            /* now we need to reduce according to our mode */
            for ( let i = 0; i < this._rawFibreShots[ 0 ].amplitudes.length; i += this.widthFactor ) {
                const v = this._modes[ this.mode ].func( i );
                if ( Math.abs( v[ 0 ] ) > Math.abs( v[ 1 ] ) ) {
                    amplitudes.push( v[ 0 ] );
                } else {
                    amplitudes.push( v[ 1 ] );
                }
            }
            this._rawFibreShots[ 0 ].amplitudes = amplitudes;
            this.command( 'run', 'added', this._rawFibreShots[ 0 ] );
            this._rawFibreShots.length = 0;
        }

        if ( reset ) {
            /* We should store the new fibreshot that's come in regardless */
            this._rawFibreShots.push( fibreShot );
        }
    }

    public setModes = function () {
        this._modes = {
            'magnitude': {
                name: 'Peak to peak',
                func: function ( pos ) {
                    let max = 0; // this._rawFibreShots[0].amplitudes[pos];
                    let min = 0; // this._rawFibreShots[0].amplitudes[pos];
                    for ( let i = 0; i < this.widthFactor; ++i ) {
                        for ( let j = 0; j < this._rawFibreShots.length; ++j ) {
                            max = Math.max( max, this._rawFibreShots[ j ].amplitudes[ pos + i ] );
                            min = Math.min( min, this._rawFibreShots[ j ].amplitudes[ pos + i ] );
                        }
                    }

                    return [ min, max ];
                }.bind( this )
            },
            'nth': {
                name: 'Nth',
                func: function ( pos ) {
                    let max = null;
                    let min = null;

                    for ( let i = 0; i < this.widthFactor; ++i ) {
                        for ( let j = 0; j < this._rawFibreShots.length; ++j ) {
                            if ( this._rawFibreShots[ j ].amplitudes[ pos + i ] >= 0 ) {
                                max = this._rawFibreShots[ j ].amplitudes[ pos + i ];
                            } else {
                                min = this._rawFibreShots[ j ].amplitudes[ pos + i ];
                            }
                        }

                        if ( max && min ) {
                            break;
                        }
                    }

                    if ( !min ) {
                        min = max;
                    }
                    if ( !max ) {
                        max = min;
                    }

                    return [ min, max ];
                }.bind( this )
            },
            'mean': {
                name: 'Average',
                func: function ( pos ) {
                    let sumP = 0;
                    let sumN = 0;
                    let countP = 0;
                    let countN = 0;

                    for ( let i = 0; i < this.widthFactor; ++i ) {
                        for ( let j = 0; j < this._rawFibreShots.length; ++j ) {
                            if ( this._rawFibreShots[ j ].amplitudes[ pos + i ] >= 0 ) {
                                sumP += this._rawFibreShots[ j ].amplitudes[ pos + i ];
                                countP++;
                            } else {
                                sumN += Math.abs( this._rawFibreShots[ j ].amplitudes[ pos + i ] );
                                countN++;
                            }
                        }
                    }

                    let avgN = ( sumN / Math.max( 1, countN ) ) * -1;

                    let avgP = sumP / Math.max( 1, countP );

                    if ( countN === 0 ) {
                        avgN = avgP;
                    }
                    if ( countP === 0 ) {
                        avgP = avgN;
                    }

                    return [
                        avgN,
                        avgP
                    ];
                }.bind( this )
            },
            'mode': {
                name: 'Most frequent',
                func: function ( pos ) {
                    const numMappingPos = {};
                    const numMappingNeg = {};

                    let greatestFreqPos = 0;
                    let greatestFreqNeg = 0;
                    let modePos = 0;
                    let modeNeg = 0;

                    let negCount = 0;
                    let posCount = 0;

                    for ( let i = 0; i < this.widthFactor; ++i ) {
                        for ( let j = 0; j < this._rawFibreShots.length; ++j ) {
                            const nbr = this._rawFibreShots[ j ].amplitudes[ pos + i ];
                            if ( nbr >= 0 ) {
                                numMappingPos[ nbr ] = ( numMappingPos[ nbr ] || 0 ) + 1;
                                posCount++;
                                if ( greatestFreqPos < numMappingPos[ nbr ] ) {
                                    greatestFreqPos = numMappingPos[ nbr ];
                                    modePos = nbr;
                                }
                            } else {
                                numMappingNeg[ nbr ] = ( numMappingNeg[ nbr ] || 0 ) + 1;
                                negCount++;
                                if ( greatestFreqNeg < numMappingNeg[ nbr ] ) {
                                    greatestFreqNeg = numMappingNeg[ nbr ];
                                    modeNeg = nbr;
                                }
                            }
                        }
                    }

                    if ( negCount === 0 ) {
                        modeNeg = modePos;
                    }

                    if ( posCount === 0 ) {
                        modePos = modeNeg;
                    }

                    return [ modeNeg, modePos ];
                }.bind( this )
            }
        };
    };

}
