import { Ant, Queen } from '@utils/ants';
import { AmplitudeCanvas } from '@utils/amplitudeCanvas';
import { Colour } from '@utils/colour';
import Reducer from '@utils/reducer';

export default class Waterfall extends AmplitudeCanvas {
    private numberToKeep: number = 0;
    private rawfibreShots: any[] = [];
    private fibreShots: any[] = [];
    private reducer: any;
    private running: boolean = true;
    private colours: Colour = new Colour();

    private ctx: CanvasRenderingContext2D;

    public min: number = -1;
    public max: number = 32768;

    public maxEncountered = 0;
    public minEncountered = 0;
    public autoscale = true;

    constructor( canvas, public feed: any ) {
        super();

        this.canvas = canvas;
        this.ctx = this.canvas.getContext( '2d' );

        console.log( 'Building Waterfall renderer' );

        console.log( 'building reducer' );
        this.reducer = new Queen( Reducer, '/javascript/reducer.bundle.js', {
            callbacks: { 'added': ( fs ) => {
                this.newFibreShot( fs );
            } }
        } );

        this.reducer.widthFactor = 1;
        this.reducer.timeFactor = 1;
        this.reducer.mode = 'magnitude';

        console.log( 'Registered Reducer', this.reducer );

        feed.subscribe( this.addToReducer.bind( this ) );
    }

    /* Playback, pause, stop and destroy etc */
    public play() {
        this.running = true;
    }

    public stop() {
        this.running = false;
    }

    /* Called when the reducer has had a new fibreshot produced */
    public newFibreShot( fibreShot: any ): void {
        this.fibreShots.unshift( fibreShot );
        this.fibreShots.length = this.numberToKeep;
    }

    public addToReducer( fibreShot: any ): void {
        /* Determine how many fibreshots to keep, truncating the array
           accordingly to keep memory usage down */

        this.numberToKeep = Math.max(
            this.canvas.clientHeight,
            this.numberToKeep,
            10
        );

        this.rawfibreShots.unshift( fibreShot );
        this.rawfibreShots.length = Math.min( this.rawfibreShots.length, this.numberToKeep );
        this.reducer.add( fibreShot );
    }

    public animate() {
        if ( !this.running ) {
            return;
        }

        if ( this.rawfibreShots.length > 0 ) {
            /* Nothing to do, so do nothing */

            /* Determine the nature of our fibreshots etc */
            const screenCanvas = this.canvas;
            const canvas = this.renderingCanvas;

            const height = Math.min(
                canvas.height - 1,
                this.fibreShots.length
            );

            let width = canvas.width - 1;

            if ( this.fibreShots.length > 0 && this.fibreShots[ 0 ].amplitudes ) {
                width = Math.min( width, this.fibreShots[ 0 ].amplitudes.length );
            }

            // let ctx = this.renderingCanvas.getContext('2d', { alpha: false });
            const ctx = this.renderingContext;

            if ( !ctx ) {
                console.log( 'No rendering context' );
                return;
            }

            /* Reset the fibreShot scales, this reduces the size of the waterfall */
            const numberOfAmplitudes = this.rawfibreShots[ 0 ].amplitudes.length;
            let changed = this.setScale( numberOfAmplitudes );

            /* dynamic colour ranges */
            if ( this.autoscale ) {
                this.max = this.maxEncountered;
                this.min = this.minEncountered;
            }

            /* Reset the colour ranges ? */
            const colourRange = this.colours.getRange();
            if ( colourRange.max !== this.max || colourRange.min !== this.min ) {
                this.colours.setRange( this.min, this.max );
                changed = true;
            }

            /*
          let image = new Uint8ClampedArray( height * width * 4 );

          for (var y = 0; y < height; ++y) {
            if (this.fibreShots[y]) {
              let row = y * width * 4;
              let pixel = 0;

              for (var x = 0; x < width; ++x) {
                if (this.fibreShots[y].amplitudes[x]) {
                  let colour = this.colours.get( this.fibreShots[y].amplitudes[x]);

                  image[row + pixel + 0] = colour.red;
                  image[row + pixel + 1] = colour.green;
                  image[row + pixel + 2] = colour.blue;
                  image[row + pixel + 3] = 255;

                  this.maxEncountered = Math.max( this.maxEncountered, this.fibreShots[y].amplitudes[x] );
                  this.minEncountered = Math.min( this.minEncountered, this.fibreShots[y].amplitudes[x] );
                }
                pixel = pixel + 4;
              }
            }
          }

          try {
            let id = new ImageData( image, width, height );
            this.ctx.putImageData( id, 0, 0, 0, 0, this.canvas.clientWidth, this.canvas.clientHeight)
          } catch (e){
          };

          return; */

            for ( let y = 0; y < height; ++y ) {
                if ( this.fibreShots[ y ] ) {
                    if ( !changed && this.fibreShots[ y ]._cached ) {
                        /* we can reuse a cached version of this rendering if we wish
                * so for now, we don't need to render it again */
                    } else {
                        const gradient = ctx.createLinearGradient( 0, 0, this.canvas.clientWidth, 0 );
                        let previousEnd = 0;

                        for ( let x = 0; x < width; ++x ) {
                            if ( this.fibreShots[ y ].amplitudes[ x ] ) {
                                const colour = this.colours.get( this.fibreShots[ y ].amplitudes[ x ] ).colour();
                                const end = Math.min( Math.min( Math.max( ( x * this.widthFactor ) / this.canvas.clientWidth, 0 ), 1 ), 1 );

                                /* track maximum and minimum values */
                                this.maxEncountered = Math.max( this.maxEncountered, this.fibreShots[ y ].amplitudes[ x ] );
                                this.minEncountered = Math.min( this.minEncountered, this.fibreShots[ y ].amplitudes[ x ] );

                                /*
                    gradient.addColorStop(previousEnd, colour);
                    gradient.addColorStop(end, colour);
                    */
                                const stop = previousEnd + ( ( end - previousEnd ) / 2 );
                                if ( isFinite( stop ) ) {
                                    gradient.addColorStop( stop, colour );
                                    // previousEnd + ( (end-previousEnd)/2), colour );
                                }
                                previousEnd = end;
                            }
                        }

                        ctx.strokeStyle = gradient;
                        ctx.lineWidth = 2;
                        ctx.beginPath();
                        ctx.moveTo( 0, 0 );
                        ctx.lineTo( this.canvas.clientWidth, 0 );
                        ctx.stroke();

                        if ( this.canvas.clientWidth > 0 ) {
                            const capturedImage = ctx.getImageData( 0, 0, this.canvas.clientWidth, 1 );
                            this.fibreShots[ y ]._cached = capturedImage;
                        }
                    }
                }
            }

            for ( let y = 0; y < height; ++y ) {
                if ( this.fibreShots[ y ] ) {
                    if ( this.fibreShots[ y ]._cached ) {
                        /* we can reuse a cached version of this rendering if we wish */
                        this.ctx.putImageData( this.fibreShots[ y ]._cached, 0, y, 0, 0, this.canvas.clientWidth, 1 );
                    }
                }
            }
        }
    }

    protected rescale(): boolean {
        this.reducer.setWidth( Math.ceil( this.widthSamplesPerPixel ) );

        this.reducer.clear();
        this.fibreShots.length = 0;
        this.rawfibreShots.reverse().forEach( element => {
            this.reducer.add( element );
        } );

        /* Draw a blank background, which will clear any previous renderings */
        this.ctx.clearRect( 0, 0, this.canvas.width, this.canvas.height );

        return true;
    }

    public get palettes() {
        return this.colours.getPalettes();
    }

    private _palette: string = 'Default';
    public get palette() {
        return this._palette;
    }

    public set palette( palette ) {
        this.colours.setPalette( palette );
    }
}

export class WaterfallWorker extends Ant {
    public width: number = 640;
    public height: number = 480;

    public render( data ): ImageBitmap | void {
    }
}
