/* Worker reflection mechanism, creates a pair of classes which are designed
   to be used as a worker thread and the parent thread and share properties
   between the two */

/* Ants are the actual workers, they do the work and let the queen take the
   credit */
export class Ant {
    public message( message: any ) {
        const command = message.data.command;
        const params = message.data.params;

        /* depending upon the nature of the command, we need to determine
           what to do, and what to reply with ... */
        switch ( command ) {
            case 'get':
                this.command( 'get', params.key, this[ params.key ] );
                break;
            case 'set':
                this[ params.key ] = params.params;
                this.command( 'set', params.key, this[ params.key ] );
                break;
            case 'run':
                const response = this[ params.key ]( params.params );
                this.command( 'run', params.key, response );
                break;
            default:
                break;
        }
    }

    public command( command: string, key: string, params: any ): void {
        ( <any>postMessage )( { command, key, params } );
    }
}

/* The Queen class controls the Ant and passes data down to it, awaiting it
   doing some work and returning stuff */

export class Queen {
    private worker: Worker;
    private awaitingReply: Object = {};
    private callbacks: Object = {};

    constructor( ant: any, workerPath: string, options = null ) {

        const example = new ant( options );
        const proto = Object.getPrototypeOf( example );

        Object.getOwnPropertyNames( example ).forEach( ( key ) => {
            const description = Object.getOwnPropertyDescriptor( example, key );

            Object.defineProperty( this, key, {
                get: () => {
                    return this.get( key );
                },
                set: ( val ) => {
                    this.set( key, val );
                },
                enumerable: true
            } );
        } );

        Object.getOwnPropertyNames( proto ).forEach( ( key ) => {
            /* Based opon the type of the property we are mapping we either
               need to create a pair of getters and setters to allow us to
               manipulate the underlying type, *or* we need to create a
               Promise wrapped function for it */
            const type = typeof( example[ key ] );

            if ( type === 'function' ) {
                if ( [ 'constructor', 'register' ].includes( key ) ) {
                    return;
                }
                /* Add this function, or at least a mirror of it */
                this[ key ] = ( params? ): Promise<any> => {
                    return this.run( key, params );
                };
            }
        } );

        if ( options.callbacks ) {
            this.callbacks = options.callbacks;
        }

        this.worker = null;
        const self = this;

        this.worker = new Worker( workerPath );
        this.worker.onmessage = function( e ) {
            self.acceptMessage( e.data );
        };
    }

    private get( key ) {
        return this.command( 'get', key );
    }

    private set( key, value ) {
        return this.command( 'set', key, value );
    }

    private run( key, params ) {
        return this.command( 'run', key, params );
    }

    private acceptMessage( message ) {
        const command = message.command;
        const key = message.key;
        const answer  = message.params;
        if ( this.awaitingReply[ key ] ) {
            this.awaitingReply[ key ].forEach( prom => {
                prom.resolve( answer );
            } );
            this.awaitingReply[ key ] = [];
        } else {
            /* we might have been sent this message out of the blue,
               we should probably respond to it regardless */
            if ( this.callbacks[ key ] ) {
                return this.callbacks[ key ]( message.params );
            }
        }
    }

    public register( command: string, callback: any ) {
        this.callbacks[ command ] = callback;
    }

    private command( command: string, key: string, params: any = null ): Promise<any> {
        return new Promise( ( resolve, reject ) => {
            if ( this.worker ) {
                /* ask the remote for its value and await the response */
                this.awaitingReply[ key ] = this.awaitingReply[ key ] || [];
                this.awaitingReply[ key ].push( { resolve, reject } );
                this.worker.postMessage( { command: command, params: { key: key, params: params } } );
            }
        } );
    }
}
